import { SubpageBackground } from 'components/molecues/image-background/image-background';
import { graphql, useStaticQuery } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';
import AboutUs from 'data/pages/about-page.mdx';

export const AboutPageContent = () => {
  const { allMdx: { nodes } } = useStaticQuery<Queries.AboutUsQuery>(graphql`
query AboutUs {
  allMdx(
    filter: {frontmatter: {type: {eq: "about-us"}}}
    limit: 1
  ) {
    nodes {
      frontmatter {
        title
      }
    }
  }
}`);

  const pageData = nodes[0];
  return (
    <div className="flex w-full grow flex-col items-center justify-center">
      <div className="flex h-screen flex-col items-center justify-center py-32 md:flex-row">
        <SubpageBackground className="h-screen md:h-screen" />
        <div className="flex h-full w-full max-w-[50%] flex-col items-center justify-center gap-4 px-2 md:max-w-full md:flex-row lg:px-24">
          <div className="flex h-fit w-full items-center md:px-32">
            <h1 className="text-center font-uni-sans text-2xl font-black uppercase md:text-left md:text-3xl lg:text-4xl">{pageData.frontmatter?.title}</h1>
          </div>
          <div className="w-fit">
            <StaticImage src="../../../images/other/computer-set.png" alt="example pc" className="aspect-[1/1]" quality={100} />
          </div>
        </div>
      </div>
      <div className="flex w-full max-w-none flex-col items-center bg-background-dark px-12 py-16 text-main-white md:px-28 lg:px-72">
        <AboutUs />
      </div>
    </div>
  );
};
